.statusContainer{
    font-family: TwitterChirp, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
    font-size: 15px;
    font-weight: 400;
    margin-bottom: 12px;
    display: flex;
    width: fit-content;
    align-items: end;
}
.statusWrapper{
    display: block;
}
.redactLogoWrapper{
    display: flex;
    height: 15px;
}
.redactLogo{
    display: inline-block;
    overflow: visible !important;
    vertical-align: text-bottom;
    fill: currentColor;
    height: 1.25em;
    max-width: 100%;
}
.redactLogo path{
     color: rgb(255, 255, 255);
 }
.cursorPointer{
    cursor: pointer;
}