
.profileInfoContainer {
    padding: 12px 16px 0 16px;
    margin-bottom: 16px;
}

.topInfoPanel {
    display: flex;
    justify-content: space-between;
    max-height: 69px;
    height: 15vw;
    width: 100%;
    min-height: 40px;
    min-width: 0;
}
.editProfileButtonWrapper {
    display: flex;
    justify-content: center;
    align-items: start;
}

.editProfileButton {
    background-color: rgba(0, 0, 0, 100);
    border-radius: 20px;
    padding: 9px 18px 9px 18px;
    cursor: pointer;
    border: 1px solid rgb(83, 100, 113);
    transition: background-color 0.2s ease-in-out
}

.editProfileButton:hover {
    background-color: rgba(239, 243, 244, 0.1);
}

.editProfileButton span {
    font-size: 15px;
    font-family: 'Helvetica Neue', sans-serif;
    font-weight: bold;
}

.name {
    font-size: 20px;
    font-weight: 800;
    font-family: TwitterChirp, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
    margin-bottom: 12px;
    display: flex;
    justify-content: start;
}

.name span {
    font: inherit;
}

.dataContainer {
    display: inline-block;
    font-size: 15px;
    font-family: TwitterChirp, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
    font-weight: 400;
}

.dataContainer span {
    color: rgb(113, 118, 123);
}
.editProfileBgShadow{
    background-color: rgba(91, 112, 131, 0.4);
    position: fixed;
    bottom: 0;
    top: 0;
    right: 0;
    left: 0;
    z-index: 0;
}
.editProfileContainer{
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    overscroll-behavior: none;
    border-radius: 16px;
    z-index: 2;
}
.scrollbarDecorator{
    position: absolute;
    z-index: 1;
    overflow: hidden;
    border-radius: 16px;
}
.editProfileWrapper{
    min-width: 600px;
    min-height: 400px;
    max-height: 90vh;

    width: 600px;
    height: 650px;
    z-index: 10;
    background-color: rgba(0, 0, 0, 1);
    overflow-y: auto;
    overflow-x: hidden;
}

.scrollbarDecorator {
    color-scheme: dark;
}

